.cred-story{
    background-color: var(--bg-green);
    min-height: 100vh;
    min-width: 100vw;
    background-image: url("https://web-images.credcdn.in/_next/assets/images/home-page/trust-bg.jpg");
    background-size: cover;
    background-position: right bottom;
    position: relative;
}

.story-wrapper{
    align-items: flex-end;
}

.cred-story-heading-wrapper{
    width: 50%;
    flex-direction: column;
    -webkit-box-pack: end;
    justify-content: flex-end;
}

.cred-story-heading{
    font-size: 60px;
    line-height: 60px;
    font-weight: 700;
    max-width: 80%;
    white-space: pre-line;
    opacity: 0.9;
}

.cred-story-detail{
    width: 50%;
    font-weight: 500;
    margin-left: 30px;
}

.cred-story-para{
    font-size: 18px;
    line-height: 28px;
    margin-top: 20px;
    max-width: 550px;
}

@media all and (max-width: 1279px) {
    .cred-story-heading{
        max-width: 100%;
    }
}
@media all and (max-width: 767px) {
    .cred-story{
        background-image: url(https://web-images.credcdn.in/_next/assets/images/home-page/trust-bg-mobile.jpg);
    }

    .cred-story-heading{
        font-size: 55px;
        line-height: 55px;
        padding-bottom: 80px;
    }

    .cred-story-para{
        font-size: 15px;
    }

    .story-wrapper{
        flex-direction: column;
        padding: 70px 0px;
    }

    .cred-story-heading-wrapper{
        width: 100%;
    }

    .cred-story-detail{
        width: 100%;
    }
}